import React from "react";
import { connect } from "react-redux";
import "./button.component.scss";

import {
  changeAppWindow,
  ResetBarcodeStatusArray
} from "../../redux/app-reducer/app.reducer.actions";

const ButtonComponent = ({ changeAppWindow, ResetBarcodeStatusArray, ...btnName }) => {
  const buttonHandler = () => {
    btnName.btnType === "windowCtrButton" ? changeAppWindow(btnName) : ResetBarcodeStatusArray();
  };

  return (
    <button
      className={btnName.classes}
      style={{ backgroundColor: btnName.color }}
      onClick={buttonHandler}
    >
      {btnName.name}
    </button>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    changeAppWindow: (item) => dispatch(changeAppWindow(item)),
    ResetBarcodeStatusArray: (statusArray) => dispatch(ResetBarcodeStatusArray(statusArray))
  };
};

export default connect(null, mapDispatchToProps)(ButtonComponent);
