import React, { useState } from "react";
import { connect } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import {
  Card,
  CardHeader,
  CardContent,
  Avatar,
  IconButton,
  Modal,
  Backdrop,
  Fade,
  Typography,
  Grid
} from "@material-ui/core";
import InfoIcon from "@material-ui/icons/Info";

import "./status.card.component.scss";

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 200,
    minHeight: 100,
    marginBottom: "15px"
  },
  avatar: {
    fontSize: ".8rem",
    backgroundColor: "",
    width: theme.spacing(5),
    height: theme.spacing(5)
  },
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3)
  }
}));

const StatusCard = ({ appReducer }) => {
  let initialResponseArr = appReducer.initialRequest.response;
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [count, setCount] = useState(0);
  const [modalData, setModalData] = useState(0);

  if (initialResponseArr.length === 0) {
    setTimeout(() => {
      initialResponseArr = appReducer.initialRequest.response;
      setCount(count + 1);
    }, 500);
  }

  setTimeout(() => {
    RotateCards();
  }, 1500);

  setTimeout(() => {
    if (initialResponseArr.length === 0) {
      DisplayAllert()
    }
  }, 2000);

  const ConvertTimestampToDate = (timestamp) => {
    let dateVar = timestamp.split(/[- :]/);
    dateVar = dateVar[2] + "-" + dateVar[1] + "-" + dateVar[0];
    return dateVar;
  };

  const ConvertTimestampToTime = (timestamp) => {
    let timeVar = timestamp.split(/[- :]/);
    timeVar = timeVar[3] + ":" + timeVar[4];
    return timeVar;
  };

  const ReturnMachineStatus = (statusArr) => {
    let result = "OK";
    for (let i = 0; i < statusArr.length; i++) {
      if (statusArr[i] === "NOK") {
        result = statusArr[i];
        break;
      }
    }
    return result;
  };

  const RotateCards = () => {
    const groupToChange = document.getElementsByClassName("card_body");
    for (var i = 0; i < groupToChange.length; i++) {
      groupToChange[i].style.transition = "all, 1s ease";
      groupToChange[i].style.transform = "rotateY(360deg)";
    }
  };

  const DisplayAllert = () => {
    const elementToChange = document.getElementById("appElements");
    const warningBlock = document.getElementById("warning_block");
    elementToChange.style.display = "none";
    warningBlock.style.display = "block";

    document.getElementById("configAddress").innerHTML = appReducer.noDataWarningResp.configTxt;
    document.getElementById("errorMsg").innerHTML = appReducer.noDataWarningResp.errorTxt;
  };

  const UpdateItemForModal = (item) => {
    setModalData(item);
    handleOpen();
  };

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  

  return (
    <div
      style={{
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "space-around",
        padding: "1rem"
      }}
    >
      {appReducer.initialRequest.response
        ? appReducer.initialRequest.response.map((item) => (
            <div key={item.id}>
              <Card className={classes.root}>
                <CardHeader
                  avatar={
                    <Avatar
                      className={classes.avatar}
                      style={{
                        backgroundColor: "#" + item.machine_status_overall,
                        textShadow: "0.5px 0.5px 2px #000"
                      }}
                    >
                      {ReturnMachineStatus(item.machine_status.split(","))}
                    </Avatar>
                  }
                  action={
                    <IconButton
                      title={ConvertTimestampToTime(item.insert_date)}
                      onClick={() => UpdateItemForModal(item)}
                    >
                      <InfoIcon />
                    </IconButton>
                  }
                  titleTypographyProps={{ variant: "caption" }}
                  subheaderTypographyProps={{ variant: "caption" }}
                  title={item.machine_name}
                  subheader={ConvertTimestampToDate(item.insert_date)}
                />
                <CardContent
                  className="card_body"
                  style={{ backgroundColor: "#" + item.machine_status_overall }}
                ></CardContent>
              </Card>
            </div>
          ))
        : ""}
        <Modal
          className={classes.modal}
          open={open}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500
          }}
        >
          <Fade in={open}>
            <div className={classes.paper} style={{opacity: "0.9", borderRadius: ".5rem"}}>
              <div style={{paddingBottom: "1rem"}}>
              <Grid container style={{justifyContent: "space-between" }}>
                <div style={{display: "flex", flexDirection: "column-reverse"}}>
                  <Typography>{modalData ? ConvertTimestampToDate(modalData.insert_date) : ""}</Typography>
                  <Typography>{modalData ? ConvertTimestampToTime(modalData.insert_date) : ""}</Typography>
                </div>
                <Typography
                  className="title-text-modal" style={{ fontSize: "1.5rem" }}>
                    {modalData ? modalData.machine_name : ""}
                </Typography>
              </Grid>
              </div>
              <Grid container>
                {modalData ? modalData.imgs_src.split(",").map((img, index) => (
                  <div key={img} className="report_container">
                    <img className="report_img" src={img} alt={"check--" + img} />
                    <Grid container className="text-content">
                      <Typography
                        className="result_text"
                        style={{ color: "#" + modalData.color_codes.split(",")[index] }}
                      >
                        {modalData.machine_status.split(",")[index]}
                      </Typography>
                    </Grid>
                  </div>
                )) : ""}
              </Grid>
            </div>
          </Fade>
        </Modal>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    appReducer: { ...state.appReducer }
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    //updateMachineStatusArray: (machineStatusArray, insertedValue, apiLocation) => dispatch(updateMachineStatusArray(machineStatusArray, insertedValue, apiLocation))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(StatusCard);
