import React, { useEffect } from 'react';
import axios from 'axios';
import { connect } from "react-redux";
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import {Drawer, IconButton, Button } from '@material-ui/core';
import MenuIcon from "@material-ui/icons/Menu";
import BackspaceIcon from "@material-ui/icons/Backspace";
import SendIcon from '@material-ui/icons/Send';

import "./request.drawer.component.scss";
import DataTableComponent from '../data-table/data.table.component';
import DatePickersComponent from '../date-picker/date.picker.component';

import { GetStatisticFromAPI } from '../../../redux/app-reducer/app.reducer.actions';

const useStyles = makeStyles((theme) => ({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
  paper: {
    padding: "1rem",
    backgroundColor: "#fafafa",
    opacity: .98
  },
  button: {
    margin: theme.spacing(1),
  },
}));

const RequestMenu = ({ appReducer, GetStatisticFromAPI }) => {
  useEffect(() => {
    if (!appReducer.detailedStatisticFromAPI) {
      DatesForAPIReq[0] = ConvertDate(lastmonth);
      DatesForAPIReq[1] = ConvertDate(today);
      GetMachineDetails(DatesForAPIReq);
    }
  });

  let DatesForAPIReq = [];
  const today = new Date();
  let lastmonth = new Date();
  lastmonth.setDate(1);
  lastmonth.setMonth(lastmonth.getMonth()-1);

  // Prepare dates, required by API to collect data from DB
  const ConvertDate = (date) => {
    var yyyy = date.getFullYear().toString();
    var mm = (date.getMonth()+1).toString();
    var dd  = date.getDate().toString();

    var mmChars = mm.split('');
    var ddChars = dd.split('');

    return yyyy + '-' + (mmChars[1]?mm:"0"+mmChars[0]) + '-' + (ddChars[1]?dd:"0"+ddChars[0]);
  }

  const GetMachineDetails = (timePeriod) => {
    axios.post(appReducer.backendLocation + "?getstatusreport=" + JSON.stringify(timePeriod))
    .then(res => {
      const dataArr = Object.entries(res.data);
      GetStatisticFromAPI(dataArr);
    });
  };

  const DateLabels = {
    data: {
      startDate: {
        data: appReducer.tableHeaderLanguages.startDate,
        date: lastmonth,
        idLable: 'Start date'
      },
      endDate: {
        data: appReducer.tableHeaderLanguages.endDate,
        date: today,
        idLable: 'End date'
      }
    }
  };

  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      //onClick={toggleDrawer(anchor, false)}
      //onKeyDown={toggleDrawer(anchor, false)}
    >
    </div>
  );

  // Collect dates which user input into the fields
  const CollectRequestedPeriod = (startDate, endDate) => {
    DatesForAPIReq[0] = startDate.replace(/(\d\d)\/(\d\d)\/(\d{4})/, "$3-$1-$2")
    DatesForAPIReq[1] = endDate.replace(/(\d\d)\/(\d\d)\/(\d{4})/, "$3-$1-$2")
    SendUserReq(DatesForAPIReq)
  };

  const SendUserReq = (datesArr) => {
    GetMachineDetails(datesArr)
  };

  return (
    <div>
      {['top'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}><MenuIcon /></Button>
          <Drawer classes={{paper: classes.paper}} anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
            <div id="date-request-container">
              <div id="date-request-form">
                <DatePickersComponent { ...DateLabels.data.startDate } />
                <DatePickersComponent { ...DateLabels.data.endDate } />
                <Button
                  variant="outlined"
                  color="primary"
                  className={classes.button}
                  endIcon={<SendIcon />}
                  onClick={() => CollectRequestedPeriod(
                    document.getElementById("start_date").value,
                    document.getElementById("end_date").value
                  )}
                >
                  {appReducer.tableHeaderLanguages.requestBtn}
                </Button>
              </div>
              <div>
                <IconButton onClick={toggleDrawer(anchor, false)}>
                  <BackspaceIcon />
                </IconButton>
              </div>
            </div>
            <DataTableComponent {...appReducer} />
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    appReducer: { ...state.appReducer }
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    GetStatisticFromAPI: (data) => dispatch(GetStatisticFromAPI(data))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RequestMenu);
