import React from "react";
import { connect } from "react-redux";
import "./footer.component.scss";

const FooterGrid = ({ appData }) => {
  return (
    <span className="footer-text splashInBottom">
      This application is made by{" "}
      <a
        className="link"
        href={appData.appAuthor.contactLink}
      >
        {appData.appAuthor.authorName}
      </a>{" "}
      in Kolomyia.
    </span>
  );
};

const mapStateToProps = (state) => {
  return {
    appData: { ...state.appReducer }
  };
};

export default connect(mapStateToProps)(FooterGrid);
