import {store} from '../root.store';

import {
  CHANGE_APP_WINDOW_MODE, 
  UPDATE_MACHINE_STATUS_ARRAY,
  RESET_MACHINE_STATUS_ARRAY,
  DB_CONNECTION_TEST,
  GET_DATA_FROM_API,
  GET_STATISTIC_FROM_API
} from "./app.reducer.types";

import {
  changeAppWindowMode, 
  HandleBarcodeScan,
  ResetStatusArray,
  CheckDBConnection,
  GetInitialData
} from "./app.reducer.utils";

export const changeAppWindow = (btnChangeAppWindow) => ({
  type: CHANGE_APP_WINDOW_MODE,
  payload: changeAppWindowMode(btnChangeAppWindow)
});

export const updateMachineStatusArray = (statusArray, barcodeData, backendAPI) => ({
  type: UPDATE_MACHINE_STATUS_ARRAY,
  payload: HandleBarcodeScan(statusArray, barcodeData, backendAPI)
});

export const ResetBarcodeStatusArray = () => ({
  type: RESET_MACHINE_STATUS_ARRAY,
  payload: ResetStatusArray()
});

export const CheckDBConnectionAction = () => ({
  type: DB_CONNECTION_TEST,
  payload: CheckDBConnection(store.getState())
});

export const GetDataFromAPI = () => ({
  type: GET_DATA_FROM_API,
  payload: GetInitialData(store.getState())
});

export const GetStatisticFromAPI = (apiResponse) => ({
  type: GET_STATISTIC_FROM_API,
  payload: apiResponse
});
