import React from "react";
import { connect } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";

import "./body.component.scss";

import ContentCard from "./content-card/content-card.component";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
    margin: theme.spacing(0.2)
  }
}));

const BodyGrid = ({ appData }) => {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Grid container>
        {appData.appContentCard.map((blockElement) => (
          <Grid key={blockElement.id} zeroMinWidth item xs>
            <ContentCard {...blockElement} />
          </Grid>
        ))}
      </Grid>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    appData: { ...state.appReducer }
  };
};

export default connect(mapStateToProps)(BodyGrid);
