export const HandleBarcodeScan = (statusArray, barcodeData, backendAPI) => {
  const MakeCardObj = (inputData) => {
    const barcodeString = inputData.split('-'); // Split the data into 3 different variables
    /**
     * Check if MachineName-ID-Status pattern was meet
     * Take color attribute from html tag and make card block object
     * If something goes wrong  just return empty object
     */
    if (barcodeString[0] && barcodeString[1] && barcodeString[1] <= 6 && barcodeString[2]) {
      const barcodeBlockColor = barcodeString[2].toUpperCase() === "OK" 
        ? document.getElementById(barcodeString[1] - 1).getAttribute("color--ok")
        : document.getElementById(barcodeString[1] - 1).getAttribute("color--nok");
      
      const objectToReturn = {
        blockId: barcodeString[1] - 1,
        // Takes all text data, remove spaces and make first letter capitalized then + rest of the string with removed spaces and makes all letters lowercase
        machineName: barcodeString[0].replace(/\s/g,'')[0].toUpperCase() + barcodeString[0].replace(/\s/g,'').slice(1).toLowerCase(),
        machineStatus: barcodeString[2].toUpperCase(),
        barcodeBlockColor: barcodeBlockColor.substring(1),
        blockImage: "img/img--" + barcodeString[1] + ".png"
      };
      return objectToReturn;
    } else {
      return null;
    }
  };

  /**
   * Take all necessary data for creating animation of barcode block
   * Dinamicaly asign rotate degree based on status value
   */
  const RotateCard = (cardID, cardColor, blockStatus) => {
    const elementToChange = document.getElementById(cardID);
    const cardRotateDegre = blockStatus === "OK"
      ? "rotateY(180deg)"
      : "rotateY(360deg)";
    elementToChange.style.backgroundColor = "#" + cardColor;
    elementToChange.style.color = "#" + cardColor;
    elementToChange.style.transition = "all, 1s ease";
    elementToChange.style.transform = cardRotateDegre;
  };

  //If user scan something wrong this function will rotate all the barcode elements to its default state
  const FalseRotateCard = () => {
    const groupToChange = document.getElementsByClassName("barcode-block");
    for (var i = 0; i < groupToChange.length; i++) {
      groupToChange[i].style.backgroundColor = "#fff";
      groupToChange[i].style.color = "#000";
      groupToChange[i].style.transition = "all, 1s ease";
      groupToChange[i].style.transform = "rotateY(0deg)";
    }
  };

  /**
   * Take scanned data array from the state and new object which need to be inserted
   * make helper variable based on wich value new data should be inserted to array or existing data should be updated
   * Check if array already contaigne data with specific sanned barcode ID
   * Update data if ID already present or insert new data
   * If there is no object with specific ID then just push new object into array
   */
  const UpdateStatusArray = (arrayToCheck, newObject) => {
    let insertNextObject = true;
    
    if (arrayToCheck.length > 0) {
      for(let i = 0; i < arrayToCheck.length; i++) {
        if (arrayToCheck[i].blockId === newObject.blockId) {
          arrayToCheck[i] = newObject;
          insertNextObject = false;
          break;
        }
      }
      if (insertNextObject) arrayToCheck.push(newObject);
    } else {
      arrayToCheck.push(newObject);
    }

    return arrayToCheck;
  };

  const PrepareArrayForBackend = (arrayToProcess, backendAPI) => {
    let stringifyArrayData;
    let resultArray = [];
    let resultColor;

    // Get machine status for each step
    stringifyArrayData = arrayToProcess.map(el => el.machineStatus).toString();
    
    if (stringifyArrayData.includes("NOK")) {
      if (arrayToProcess[0].machineStatus === "NOK") {
        resultColor = arrayToProcess[0].barcodeBlockColor
      } else if (arrayToProcess[1].machineStatus === "NOK") {
        resultColor = arrayToProcess[1].barcodeBlockColor
      } else {
        for (var i = 0; i < arrayToProcess.length; i++) {
          if (arrayToProcess[i].machineStatus === "NOK") {
            resultColor = arrayToProcess[i].barcodeBlockColor
          }
        }
      }
    } else {
      resultColor = arrayToProcess[0].barcodeBlockColor
    }

    // Get machine name where scanning procedure done
    resultArray[0] = arrayToProcess[0].machineName;
    resultArray[1] = resultColor;
    resultArray[2] = arrayToProcess.map(el => el.machineStatus).toString();;
    resultArray[3] = arrayToProcess.map(el => el.blockImage).toString();
    resultArray[4] = arrayToProcess.map(el => el.barcodeBlockColor).toString();

    SendRequest(backendAPI, resultArray);
  };

  const SendRequest = (APIrequest, dataToSend) => {
    fetch(APIrequest + '?senddata=' + JSON.stringify(dataToSend))
    .then(res => res.json())
    .then(res => {
      //console.log(res);
      InsertionRespond();
    })
    .catch(error => {
      //console.log(error);
    })
  };

  const InsertionRespond = () => {
    const mainElement = document.getElementById("appElements");
    const messageBlock = document.getElementById("success_respond");
    mainElement.style.display = "none";
    messageBlock.style.display = "block";

    setTimeout(() => {
      messageBlock.style.display = "none";
      mainElement.style.display = "block";
    }, 1000);
  };

  //Komax203-5-NOK pattern for barcode scanner data

  //If new object was succesfully created then make card rotation, array filling and return the result
  const machineDataObj = MakeCardObj(barcodeData);
  if (machineDataObj) {
    if (statusArray.length < 1) FalseRotateCard();
    RotateCard(machineDataObj.blockId, machineDataObj.barcodeBlockColor, machineDataObj.machineStatus);
    
    let arrayToExport = UpdateStatusArray(statusArray ? statusArray : [], machineDataObj);
    //console.log(arrayToExport);
    
    arrayToExport.sort((a,b) => (a.blockId > b.blockId) ? 1 : ((b.blockId > a.blockId) ? -1 : 0)); // This simple function will sort array by id
    //console.log(arrayToExport);
    if (arrayToExport.length === 6) {
      PrepareArrayForBackend(arrayToExport, backendAPI);
      arrayToExport = [];
    }

    return arrayToExport;
  } else {
    FalseRotateCard();
  }
};

export const ResetStatusArray = () => {
  const arrayToReturn = [];
  const FalseRotateCard = () => {
    const groupToChange = document.getElementsByClassName("barcode-block");
    for (var i = 0; i < groupToChange.length; i++) {
      groupToChange[i].style.backgroundColor = "#fff";
      groupToChange[i].style.color = "#000";
      groupToChange[i].style.transition = "all, 1s ease";
      groupToChange[i].style.transform = "rotateY(0deg)";
    }
  };

  FalseRotateCard();
  return arrayToReturn;
};

export const CheckDBConnection = (APIrequest) => {
  const respond = {
    errorMsg: '',
    configAddress: ''
  };
  const SendRequest = (APIrequest) => {
    fetch(APIrequest)
    .then(res => res.json())
    .then(res => {
      respond.configAddress = res[0];
      respond.errorMsg = res[1];
    })
    .catch(error => {
      //console.log(error);
    })
  }
  SendRequest(APIrequest.appReducer.backendLocation);
  
  return respond;
};

export const GetInitialData = (APIrequest) => {
  const respond = {
    isResponseEmpty: false,
    response: []
  };

  const SendRequest = (APIrequest) => {
  fetch(APIrequest)
    .then(res => res.json())
    .then(res => {
      //console.log(res);
      respond.response = res;
    })
    .catch(error => {
      //console.log(error);
    })
  }

  SendRequest(APIrequest.appReducer.backendLocation + "?getlateststatusdata");
  
  return respond;
};