import React from "react";
import { Link } from 'react-router-dom';

import { connect } from "react-redux";
import CssBaseline from "@material-ui/core/CssBaseline";
import { NoSsr, Box, IconButton } from '@material-ui/core';
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import "@fontsource/roboto";
import "./container.component.scss";

// Import components
//import PersistentDrawerLeft from "../action-drawer/action.drawer.component";
import TitleComponent from "../../title/title.component";
import MenuDrawer from "../request-drawer/request.drawer.component";
import StatusCard from '../status-card/status.card.component';
import FooterGrid from "../../footer/footer.component";

import { CheckDBConnectionAction, GetDataFromAPI } from "../../../redux/app-reducer/app.reducer.actions";

const StatusContainer = ({ appReducer }) => {
  // Check data after reducer received response from server
  setTimeout(() => {
    if (appReducer.connectionCheckRespond.errorMsg) {
      DisplayError(appReducer.connectionCheckRespond);
    }
  }, 3000);

  const DisplayError = (errorRespond) => {
    const elementToChange = document.getElementById("appElements");
    const warningBlock = document.getElementById("warning_block");
    elementToChange.style.display = "none";
    warningBlock.style.display = "block";

    const isErrorMessageEmpty = document.getElementById("errorMsg").textContent;
    if (!isErrorMessageEmpty) {
      document.getElementById("configAddress").innerHTML = appReducer.connectioErrorTxt.configTxt + errorRespond.configAddress;
      document.getElementById("errorMsg").innerHTML = appReducer.connectioErrorTxt.errorTxt + errorRespond.errorMsg;
    }
  };
  
  return (
    <React.Fragment>
      <CssBaseline />
      <NoSsr>
        <Box p={2} bgcolor="secondary.main" color="primary.contrastText" id="warning_block">
          <Typography variant="h5" component="h5">{appReducer.connectioErrorTxt.msgTitle}</Typography>
          <ul>
            <li className="error_list_items" id="errorMsg"></li>
            <li className="error_list_items" id="configAddress"></li>
          </ul>
        </Box>
        <Box p={2} color="primary.contrastText" id="success_respond">
          {appReducer.successResponFromServer}
        </Box>
      </NoSsr>
      <Container maxWidth="xl" id="appContainer">
        <Typography component="div" style={{ height: "100vh" }} id="appElements">
          <div className="content">
            <Grid style={{padding: "1rem"}} container>
              <Grid item xs container style={{justifyContent: "flex-start"}}>
                <MenuDrawer />
              </Grid>
              <Grid item xs>
                <TitleComponent {...appReducer}/>
              </Grid>
              <Grid item xs></Grid>
            </Grid>
            <StatusCard />
          </div>
          <IconButton id="left_link_icon" component={Link} to={'/'}>
            <ArrowBackIosIcon />
          </IconButton>
          <FooterGrid className="footer" />
        </Typography>
      </Container>
    </React.Fragment>
  );
}

const mapStateToProps = (state) => {
  return {
    appReducer: { ...state.appReducer }
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    CheckDBConnectionAction: dispatch(CheckDBConnectionAction()),
    GetDataFromAPI: dispatch(GetDataFromAPI())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(StatusContainer);
