import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';

import "./data.table.component.scss";

const useRowStyles = makeStyles({
  root: {
    '& > *': {
      borderBottom: 'unset',
    }
  },
});

let getAppReducerData;

function createData(statusData) {
  return {
    name: statusData.name,
    status: statusData.status,
    history: statusData.history,
  };
}

function Row(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();

  return (
    <React.Fragment>
      <TableRow className={classes.root}>
        <TableCell>
          <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {row.name}
        </TableCell>
        <TableCell align="center">{row.status}</TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              <Typography variant="h6" gutterBottom component="div">
                { getAppReducerData.tableHeaderLanguages.detailsHistory }
              </Typography>
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell>{getAppReducerData.tableHeaderLanguages.statusDate}</TableCell>
                    <TableCell align="left">{getAppReducerData.tableHeaderLanguages.machineStatusDetails}</TableCell>
                    <TableCell align="center">{getAppReducerData.tableHeaderLanguages.affectedComponents}</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {row.history.map((historyRow) => (
                    <TableRow key={historyRow.date}>
                      <TableCell component="th" scope="row">
                        {historyRow.date}
                      </TableCell>
                      <TableCell align="left">{historyRow.status}</TableCell>
                      <TableCell 
                        style={{ justifyContent: "center" }} 
                        align="center"
                      >
                        {historyRow.img.map((value, index) => (
                        <img className="affected_block--img" key={index} src={value} alt="affected_component" />
                      ))}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

Row.propTypes = {
  row: PropTypes.shape({
    status: PropTypes.string.isRequired,
    history: PropTypes.arrayOf(
      PropTypes.shape({
        img: PropTypes.array.isRequired,
        status: PropTypes.string.isRequired,
        date: PropTypes.string.isRequired
      }),
    ).isRequired,
    name: PropTypes.string.isRequired
  }).isRequired,
};

const DataTableComponent = ({...appReducer}) => {
  getAppReducerData = appReducer;
  const rows = [];
  appReducer.detailedStatisticFromAPI.map((data) => {
    const dataSubArr = [];
    data[1].insert_date.map((value, index) => {
      return dataSubArr.push({
        date: value,
        status: data[1].machine_status[index],
        img: data[1].imgs_src[index]
      })
    });
    
    const dataObj = {
      name: data[0],
      status: data[1].machine_status_overall[0],
      history: dataSubArr
    };
    
    return (rows.push(createData(dataObj)))
  });
  //console.log(dataToSend);

  /*appReducer.detailedStatisticFromAPI.map((data) => {
    return (rows.push(createData(data)))
  });*/
  return (
    <TableContainer component={Paper}>
      <Table style={{ borderCollapse: 'unset' }}>
        <TableHead>
          <TableRow>
            <TableCell />
            {/*<TableCell>Dessert (100g serving)</TableCell>
            <TableCell align="right">Calories</TableCell>
            <TableCell align="right">Fat&nbsp;(g)</TableCell>
            <TableCell align="right">Carbs&nbsp;(g)</TableCell>
            <TableCell align="right">Protein&nbsp;(g)</TableCell>*/}
            <TableCell align="left">{appReducer.tableHeaderLanguages.machineName}</TableCell>
            <TableCell align="center">{appReducer.tableHeaderLanguages.machineStatus}</TableCell>
            <TableCell />
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <Row key={row.name} row={row} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default DataTableComponent;
