import { HashRouter, Route, Switch } from "react-router-dom";
import "./App.css";

import AppContainer from "./components/container/container.component";
import StatusContainer from "./components/status-addon/container/container.component";

function App() {
  return (
    <div className="App">
      <HashRouter basename="/">
        <Switch>
          <Route component={StatusContainer} exact path="/status"></Route>
          <Route component={AppContainer} exact path="/"></Route>
        </Switch>
      </HashRouter>
    </div>
  );
}

export default App;
