import React from "react";
import { connect } from "react-redux";
import "./barcode.reader.component.scss";

import { updateMachineStatusArray } from "../../redux/app-reducer/app.reducer.actions";

const BarcodeComponent = ({appReducer, updateMachineStatusArray}) => {
  const handleChange = (e) => {
    if (e.keyCode === 13) {
      const insertedValue = document.getElementById("barcode").value
      updateMachineStatusArray(appReducer.machineStatusArray, insertedValue, appReducer.backendLocation);
      document.getElementById("barcode").value = "";
    }
  };
  return (
    <div>
      <input
        id="barcode"
        autoFocus
        onKeyUp={handleChange}
      />
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    appReducer: { ...state.appReducer }
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateMachineStatusArray: (machineStatusArray, insertedValue, apiLocation) => dispatch(updateMachineStatusArray(machineStatusArray, insertedValue, apiLocation))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BarcodeComponent);
