import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import { connect } from "react-redux";

import ButtonComponent from "../button/button.component";
import TitleComponent from "../title/title.component";

//import { changeAppWindow } from "../../redux/app-reducer/app.reducer.actions";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
    marginTop: theme.spacing(2.5),
    margin: theme.spacing(0.5)
  },
  headerElements: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(0.5)
  }
}));

const HeaderGrid = ({ appData }) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item xs className={classes.headerElements}>
          <ButtonComponent {...appData.appResetBtn} />
        </Grid>
        <Grid item xs className={classes.headerElements}>
          <Typography>
            <TitleComponent {...appData} />
          </Typography>
        </Grid>
        <Grid item xs className={classes.headerElements}>
          <ButtonComponent {...appData.appWindowBtn} />
        </Grid>
      </Grid>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    appData: { ...state.appReducer }
  };
};

/*const mapDispatchToProps = (dispatch) => {
  return {
    changeAppWindow: () => dispatch(changeAppWindow())
  };
};*/

export default connect(mapStateToProps)(HeaderGrid);
