import INITIAL_STATE from "../root.state";
import {
  CHANGE_APP_WINDOW_MODE, 
  UPDATE_MACHINE_STATUS_ARRAY,
  RESET_MACHINE_STATUS_ARRAY,
  DB_CONNECTION_TEST,
  GET_DATA_FROM_API,
  GET_STATISTIC_FROM_API
} from "./app.reducer.types";

const appReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case CHANGE_APP_WINDOW_MODE:
      return {
        ...state,
        appWindowBtn: {
          ...state.appWindowBtn,
          name: action.payload.name,
          oppositBtnName: action.payload.oppositBtnName
        }
      };
    case UPDATE_MACHINE_STATUS_ARRAY:
      return {
        ...state,
        machineStatusArray: action.payload
      };
    case RESET_MACHINE_STATUS_ARRAY:
      return {
        ...state,
        machineStatusArray: action.payload
      };
    case DB_CONNECTION_TEST:
      return {
        ...state,
        connectionCheckRespond: action.payload
      };
    case GET_DATA_FROM_API:
      return {
        ...state,
        initialRequest: action.payload
      };
    case GET_STATISTIC_FROM_API:
      return {
        ...state,
        detailedStatisticFromAPI: action.payload
      };
    default:
      return state;
  }
};

export default appReducer;
