import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Paper from "@material-ui/core/Paper";

import "./content-card.component.scss";

const ContentCard = ({ ...blockElement }) => {
  const useStyles = makeStyles({
    textBlock: {
      backgroundColor: blockElement.textBlockStyles.backgroundColor,
      color: blockElement.textBlockStyles.color,
      height: "23vh",
      padding: ".5rem",
      marginTop: "-0.3rem",
      margin: "0.1rem",
      overflow: "auto"
    }
  });

  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <Paper elevation={1}>
        <p className="block-header">{blockElement.header}</p>
      </Paper>
      <Paper elevation={3}>
        <p className="action-call">{blockElement.action}</p>
      </Paper>
      <Paper elevation={3} className={classes.textBlock}>
        {blockElement.textContent.map((blockElement) => (
          <p key={blockElement} className="text-block">
            {blockElement}
          </p>
        ))}
      </Paper>

      <CardContent>
        <img
          component="img"
          className="img-property"
          alt="Komax machine"
          src={blockElement.image}
        />
      </CardContent>
      <Paper 
        id={blockElement.id} 
        className="barcode-block" 
        color--ok={blockElement.barcodeColors.ok}
        color--nok={blockElement.barcodeColors.nok}
        elevation={3}
      >
        <p className="barcode-p">{blockElement.barcodeMsg}</p>
      </Paper>
    </Card>
  );
};

export default ContentCard;
